var _ = Package.underscore._,
    package_name = "telescope:search",
    namespace = "telescope:search";

if (package_name != "project") {
    namespace = TAPi18n.packages[package_name].namespace;
}
if(_.isUndefined(TAPi18n.translations["it"])) {
  TAPi18n.translations["it"] = {};
}

if(_.isUndefined(TAPi18n.translations["it"][namespace])) {
  TAPi18n.translations["it"][namespace] = {};
}

_.extend(TAPi18n.translations["it"][namespace], {"load_more":"Carica altro","search":"Ricerca","see_what_people_are_searching_for":"Vedi cosa le persone stanno cercando."});
TAPi18n._registerServerTranslator("it", namespace);
