var _ = Package.underscore._,
    package_name = "telescope:search",
    namespace = "telescope:search";

if (package_name != "project") {
    namespace = TAPi18n.packages[package_name].namespace;
}
if(_.isUndefined(TAPi18n.translations["fr"])) {
  TAPi18n.translations["fr"] = {};
}

if(_.isUndefined(TAPi18n.translations["fr"][namespace])) {
  TAPi18n.translations["fr"][namespace] = {};
}

_.extend(TAPi18n.translations["fr"][namespace], {"load_more":"Charger la suite","search":"Rechercher","search_logs":"Historique de recherches","see_what_people_are_searching_for":"Voir ce que les gens recherchent."});
TAPi18n._registerServerTranslator("fr", namespace);
